.aboutDiv {
    display: flex;
    padding: 5% 10% 5% 10%;
    background: linear-gradient(169deg, #0891b2 65%, transparent 50%);
    flex-wrap: wrap;
    font-family: sans-serif;
}

.portfolioImg {
    box-shadow: 5px 5px rgba(0, 0, 0, .2);
    width: 60%;
}

.name {
    font-family: cursive;
    text-align: center;
    color: white;
}

.textDiv {
    padding: 3%;
    margin: 40px 0 30px 0;
    background-color: white;
    box-shadow: 5px 5px rgba(0, 0, 0, .2);
    max-width: 300px;
    /* width: 30%; */
}

.navButtons {
    display: flex;
    margin-bottom: 5px;
    justify-content: space-around;
    flex-wrap: wrap;
}

.navButton {
  background-color: #0095ff;
  border: 1px solid transparent;
  border-radius: 3px;
  box-shadow: rgba(255, 255, 255, .4) 0 1px 0 0 inset;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-family: -apple-system,system-ui,"Segoe UI","Liberation Sans",sans-serif;
  font-size: 13px;
  font-weight: 400;
  line-height: 1.15385;
  margin: 0;
  outline: none;
  padding: 8px .8em;
  position: relative;
  text-align: center;
  text-decoration: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: baseline;
  white-space: nowrap;
}

.navButton:hover,
.navButton:focus {
  background-color: #07c;
}

.navButton:focus {
  box-shadow: 0 0 0 4px rgba(0, 149, 255, .15);
}

.navButton:active {
  background-color: #0064bd;
  box-shadow: none;
}

.svgDiv {
    display: flex;
    justify-content: flex-end;
    gap: 10px;
}

.SVG {
    width: 50px;
}

.clickForProjects{
    cursor: pointer;
}

@media (max-width: 1000px) and (min-width: 500px){
    .aboutDiv {
        padding: 10% 10% 5% 10%;
        background: linear-gradient(169deg, #0891b2 65%, transparent 50%);
        display:block;
    }
    .portfolioImg {
        /* position: absolute; */
        width: 60%;
        float: left;
        margin-right: 12px;
    }
    .textDiv {
        padding: 3%;
        background-color: white;
        box-shadow: 5px 5px rgba(0, 0, 0, .2);
        max-width: 80%;
        margin-left: 20px;
        width: auto;
    }
    .emptySpace {
        width: 200px;
        height: 100px;
        border: 1px solid black;
    }
}

@media (max-width: 500px) {
    .portfolioImg {
            width: 90%;
        }
}